import { Component, Host, h, Prop } from '@stencil/core';
import { Memoize, parseIfString } from '@springbokagency/stencil-base';
import { TestBadge } from './test-badge.types';

/**
 * Test Badge component.
 */
@Component({
  tag: 'bs-test-badge-slider',
  styleUrl: 'test-badge.scss',
  shadow: true,
})
export class BsTestBadge {
  /** Props */
  /**
   * The Experience Zone contract.
   */
  @Prop() testBadgeCollection: TestBadge[] | string;

  @Memoize('testBadgeCollection')
  get parsedTestBadgeCollection(): TestBadge[] {
    return parseIfString(this.testBadgeCollection);
  }

  render() {
    return (
      <Host>
        <bs-slider>
          {this.parsedTestBadgeCollection.map((testBadge, index) => (
            <bs-click-action
              class="card"
              slot="slider-item"
              name="open-test-badge-modal"
              tagClass="click-action"
              tabIndex={1}
              value={{ index, testBadgeCollection: this.parsedTestBadgeCollection }}
            >
              <bs-test-badge-card
                cardTitle={testBadge.cardTitle}
                cardText={testBadge.cardText}
                cardImage={testBadge.badgeImages[0]}
              />
            </bs-click-action>
          ))}
        </bs-slider>
      </Host>
    );
  }
}
